<template>
  <div class="content">
    <b-container v-if="userStatus === this.CUSTOMER">
      <Responses v-if="$route.params.type === 'responses'" />
      <RequestsCustomer v-if="$route.params.type === 'requests'" />
    </b-container>

    <b-container v-else-if="userStatus === this.CONTRACTOR">
      <ResponsesContractor v-if="$route.params.type === 'responses'" />
      <RequestsContractor v-if="$route.params.type === 'requests'" />
    </b-container>
  </div>
</template>

<script>
import Responses from "../components/allResponses/Responses";
import RequestsContractor from "../components/allRequests/requestsContractor/RequestsContractor";
import RequestsCustomer from "../components/allRequests/requestsCustomer/RequestsCustomer";
import ResponsesContractor from "../components/allResponses/responsesContractor/ResponsesContractor";

export default {
  name: "Content",
  components: {
    RequestsCustomer,
    RequestsContractor,
    Responses,
    ResponsesContractor
  },
  data: () => ({
    CUSTOMER: "customer",
    CONTRACTOR: "contractor"
  }),
  computed: {
    userStatus() {
      return this.$store.getters.userStatus;
    }
  }
};
</script>

<style lang="scss" scoped>
.content {
  overflow: auto;
  height: calc(100vh - #{$header-height});
}
</style>
