<template>
  <b-card no-body class="mb-3 request" v-if="responsesByRequestId.length > 0">
    <b-card-header
      header-tag="header"
      class="p-1 d-flex pr-3 pl-3 header"
      role="tab"
      @click="collapse(request.id.toString())"
    >
      <div class="request-header">
        <h2>{{ request.name }}</h2>
        <span class="mr-3">Город: {{ request.city }}</span>
        <span>Бютжет: {{ request.budjet }}</span>
      </div>
      <button class="ml-auto">
        <img :class="shevroneClass" src="./img/shevrone_down.svg" />
      </button>
    </b-card-header>
    <b-collapse class="responds" :id="request.id.toString()">
      <b-card-body>
        <ResponseCard
          v-for="response in responsesByRequestId"
          :key="response.id"
          :response="response"
        />
      </b-card-body>
    </b-collapse>
  </b-card>
</template>

<script>
import ResponseCard from "./responsesContractor/ResponseCard";

export default {
  name: "ResponsesGroup",
  data: () => ({
    isOpen: false
  }),
  methods: {
    collapse(id) {
      this.isOpen = !this.isOpen;
      this.$root.$emit("bv::toggle::collapse", id);
    }
  },
  props: ["request"],
  computed: {
    shevroneClass() {
      if (this.isOpen) return "shevron-down";
      return "";
    },
    responsesByRequestId() {
      return this.$store.getters.getResponseById(this.request.id);
    }
  },
  components: {
    ResponseCard
  },
  mounted() {
    //this.$store.dispatch("fetchRespondById", this.request.id); uncomment after tests
  }
};
</script>

<style lang="scss" scoped>
.request {
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.35);

  &:hover {
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.55);
  }

  .request-header {
  }

  button {
    background: transparent;
    border: none;
    width: 47px;

    img {
      height: 30px;
    }

    &:focus {
      outline: none;
    }
  }
}

.shevron-down {
  transform: rotate(-90deg);
}

.header {
  cursor: pointer;
  background-color: $white;
}
</style>
