<template>
  <b-col md="6" lg="6" xl="4" class="request-box" @click="goToRequest">
    <div class="request">
      <h2>{{ request.name }}</h2>
      <div class="info">
        <span class="type">{{ request.type }}</span>
        <span class="date-created">{{ response.status }}</span>
        <!-- <span>Изменено: {{ moment(request.updated_at).format("LL") }}</span> -->
      </div>
      <hr />
      <p class="address">{{ request.adress }}</p>
      <p class="description">{{ response.description }}</p>
      <hr />
      <div class="condition">
        <span>{{ response.period }}</span>
        <span>{{ response.cost }}</span>
      </div>
    </div>
  </b-col>
</template>

<script>
import moment from "moment";
import localization from "moment/locale/ru";
moment.locale("ru", localization);

export default {
  name: "ResponseCard",
  props: {
    response: Object
  },
  data: () => ({}),
  methods: {
    goToRequest() {
      this.$router.push("/responses/" + this.response.id);
    },
    moment
  },
  computed: {
    request() {
      return this.$store.getters.getRequestById(this.response.request_id);
    }
  }
};
</script>

